@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    position: relative;
    background: #f4f1eb;
    overflow: hidden;
}

a {
    text-decoration: none;
    color: inherit;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

.App {
    text-align: center;
}

:root {
    --text-color-neon: rgb(235, 69, 180);
    --border-color: #56c1ff;
    --animation-time: 3s;
    --text-shadow: 0 0 5px rgb(167, 2, 145), 0 0 10px rgb(94, 3, 59),
        0 0 5px var(--text-color-neon), 0 0 10px var(--text-color-neon),
        0 0 10px var(--text-color-neon), 0 0 10px var(--text-color-neon),
        0 0 10px var(--text-color-neon), 0 0 20px var(--text-color-neon),
        0 0 30px var(--text-color-neon), 0 0 40px var(--text-color-neon),
        0 0 55px var(--text-color-neon), 0 0 75px var(--text-color-neon);

    --box-shadow: inset 0 0 50px var(--border-color),
        0 0 10px var(--border-color), 0 0 20px var(--border-color),
        0 0 30px var(--border-color);
}

.neon {
    background-color: transparent;
    border: 3px solid rgba(255, 255, 255, 0.33);
    border-radius: 20px;
    padding: 25px 30px;
    transform: rotate(-6deg);
    -webkit-animation: turnOnBorder 0.24s forwards step-end,
        flickerBorder 4s infinite step-end 1s;
            animation: turnOnBorder 0.24s forwards step-end,
        flickerBorder 4s infinite step-end 1s;

    font-family: "Dancing Script", cursive;
    font-weight: bolder;
}

p {
    color: rgb(251, 232, 255);
}

.neon span {
    font-size: 3.5em;
    letter-spacing: 1rem;
    text-transform: uppercase;
}

p span {
    opacity: 0.33;
    -webkit-animation: turnOn forwards step-end;
            animation: turnOn forwards step-end;
}

p span:nth-child(1) {
    -webkit-animation-delay: 0.3s;
            animation-delay: 0.3s;
}

p span:nth-child(2) {
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
}

p span:nth-child(3) {
    -webkit-animation-delay: 0.1s;
            animation-delay: 0.1s;
}
/* flicking char */
p span:nth-last-child(1) {
    -webkit-animation: turnOn 3.4s forwards step-end,
        flicker 3s infinite step-end 2s;
            animation: turnOn 3.4s forwards step-end,
        flicker 3s infinite step-end 2s;
    -webkit-animation: turnOn 3.4s forwards step-end,
        flicker var(--animation-time) infinite step-end 2s;
            animation: turnOn 3.4s forwards step-end,
        flicker var(--animation-time) infinite step-end 2s;
}

@-webkit-keyframes turnOnBorder {
    100% {
        border: 2px solid inherit;
        box-shadow: inset 0 0 50px #56c1ff,
        0 0 10px #56c1ff, 0 0 20px #56c1ff,
        0 0 30px #56c1ff;
        box-shadow: var(--box-shadow);
        opacity: 1;
    }
}

@keyframes turnOnBorder {
    100% {
        border: 2px solid inherit;
        box-shadow: inset 0 0 50px #56c1ff,
        0 0 10px #56c1ff, 0 0 20px #56c1ff,
        0 0 30px #56c1ff;
        box-shadow: var(--box-shadow);
        opacity: 1;
    }
}

@-webkit-keyframes flickerBorder {
    0% {
        box-shadow: inset 0 0 50px #56c1ff,
        0 0 10px #56c1ff, 0 0 20px #56c1ff,
        0 0 30px #56c1ff;
        box-shadow: var(--box-shadow);
    }
    99% {
        box-shadow: none;
    }
}

@keyframes flickerBorder {
    0% {
        box-shadow: inset 0 0 50px #56c1ff,
        0 0 10px #56c1ff, 0 0 20px #56c1ff,
        0 0 30px #56c1ff;
        box-shadow: var(--box-shadow);
    }
    99% {
        box-shadow: none;
    }
}

@-webkit-keyframes turnOn {
    100% {
        text-shadow: 0 0 5px rgb(167, 2, 145), 0 0 10px rgb(94, 3, 59),
        0 0 5px rgb(235, 69, 180), 0 0 10px rgb(235, 69, 180),
        0 0 10px rgb(235, 69, 180), 0 0 10px rgb(235, 69, 180),
        0 0 10px rgb(235, 69, 180), 0 0 20px rgb(235, 69, 180),
        0 0 30px rgb(235, 69, 180), 0 0 40px rgb(235, 69, 180),
        0 0 55px rgb(235, 69, 180), 0 0 75px rgb(235, 69, 180);
        text-shadow: var(--text-shadow);
        opacity: 1;
    }
}

@keyframes turnOn {
    100% {
        text-shadow: 0 0 5px rgb(167, 2, 145), 0 0 10px rgb(94, 3, 59),
        0 0 5px rgb(235, 69, 180), 0 0 10px rgb(235, 69, 180),
        0 0 10px rgb(235, 69, 180), 0 0 10px rgb(235, 69, 180),
        0 0 10px rgb(235, 69, 180), 0 0 20px rgb(235, 69, 180),
        0 0 30px rgb(235, 69, 180), 0 0 40px rgb(235, 69, 180),
        0 0 55px rgb(235, 69, 180), 0 0 75px rgb(235, 69, 180);
        text-shadow: var(--text-shadow);
        opacity: 1;
    }
}

@-webkit-keyframes flicker {
    0% {
        text-shadow: 0 0 5px rgb(167, 2, 145), 0 0 10px rgb(94, 3, 59),
        0 0 5px rgb(235, 69, 180), 0 0 10px rgb(235, 69, 180),
        0 0 10px rgb(235, 69, 180), 0 0 10px rgb(235, 69, 180),
        0 0 10px rgb(235, 69, 180), 0 0 20px rgb(235, 69, 180),
        0 0 30px rgb(235, 69, 180), 0 0 40px rgb(235, 69, 180),
        0 0 55px rgb(235, 69, 180), 0 0 75px rgb(235, 69, 180);
        text-shadow: var(--text-shadow);
    }

    30% {
        text-shadow: none;
    }
    33% {
        text-shadow: 0 0 5px rgb(167, 2, 145), 0 0 10px rgb(94, 3, 59),
        0 0 5px rgb(235, 69, 180), 0 0 10px rgb(235, 69, 180),
        0 0 10px rgb(235, 69, 180), 0 0 10px rgb(235, 69, 180),
        0 0 10px rgb(235, 69, 180), 0 0 20px rgb(235, 69, 180),
        0 0 30px rgb(235, 69, 180), 0 0 40px rgb(235, 69, 180),
        0 0 55px rgb(235, 69, 180), 0 0 75px rgb(235, 69, 180);
        text-shadow: var(--text-shadow);
    }

    35% {
        text-shadow: none;
    }
    37% {
        text-shadow: 0 0 5px rgb(167, 2, 145), 0 0 10px rgb(94, 3, 59),
        0 0 5px rgb(235, 69, 180), 0 0 10px rgb(235, 69, 180),
        0 0 10px rgb(235, 69, 180), 0 0 10px rgb(235, 69, 180),
        0 0 10px rgb(235, 69, 180), 0 0 20px rgb(235, 69, 180),
        0 0 30px rgb(235, 69, 180), 0 0 40px rgb(235, 69, 180),
        0 0 55px rgb(235, 69, 180), 0 0 75px rgb(235, 69, 180);
        text-shadow: var(--text-shadow);
    }

    99% {
        text-shadow: none;
    }
}

@keyframes flicker {
    0% {
        text-shadow: 0 0 5px rgb(167, 2, 145), 0 0 10px rgb(94, 3, 59),
        0 0 5px rgb(235, 69, 180), 0 0 10px rgb(235, 69, 180),
        0 0 10px rgb(235, 69, 180), 0 0 10px rgb(235, 69, 180),
        0 0 10px rgb(235, 69, 180), 0 0 20px rgb(235, 69, 180),
        0 0 30px rgb(235, 69, 180), 0 0 40px rgb(235, 69, 180),
        0 0 55px rgb(235, 69, 180), 0 0 75px rgb(235, 69, 180);
        text-shadow: var(--text-shadow);
    }

    30% {
        text-shadow: none;
    }
    33% {
        text-shadow: 0 0 5px rgb(167, 2, 145), 0 0 10px rgb(94, 3, 59),
        0 0 5px rgb(235, 69, 180), 0 0 10px rgb(235, 69, 180),
        0 0 10px rgb(235, 69, 180), 0 0 10px rgb(235, 69, 180),
        0 0 10px rgb(235, 69, 180), 0 0 20px rgb(235, 69, 180),
        0 0 30px rgb(235, 69, 180), 0 0 40px rgb(235, 69, 180),
        0 0 55px rgb(235, 69, 180), 0 0 75px rgb(235, 69, 180);
        text-shadow: var(--text-shadow);
    }

    35% {
        text-shadow: none;
    }
    37% {
        text-shadow: 0 0 5px rgb(167, 2, 145), 0 0 10px rgb(94, 3, 59),
        0 0 5px rgb(235, 69, 180), 0 0 10px rgb(235, 69, 180),
        0 0 10px rgb(235, 69, 180), 0 0 10px rgb(235, 69, 180),
        0 0 10px rgb(235, 69, 180), 0 0 20px rgb(235, 69, 180),
        0 0 30px rgb(235, 69, 180), 0 0 40px rgb(235, 69, 180),
        0 0 55px rgb(235, 69, 180), 0 0 75px rgb(235, 69, 180);
        text-shadow: var(--text-shadow);
    }

    99% {
        text-shadow: none;
    }
}

.pc-container {
    perspective: 1000px;
}

.pc-card {
    min-height: 70%;
    width: 40vh;
    box-shadow: 0 20px 20px rgba(0, 0, 0, 0.2), 0 0 50px rgba(0, 0, 0, 0.2);
    border-radius: 30px;
    padding: 3rem 1rem;
    transform-style: preserve-3d;
    background: #353535ef;
    /* cursor: pointer; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.pc-pic {
    min-height: 35vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.img {
    width: 12rem;
    z-index: 2 !important;
    transition: all 0.75s ease-out;
}

.pc-bg-circle {
    width: 12rem;
    height: 12rem;
    border-radius: 50%;
    background: linear-gradient(
        rgba(245, 70, 66, 0.75),
        rgba(8, 83, 156, 0.75)
    );
    position: absolute;
    z-index: 1;
    transition: all 0.5s ease-out;
    box-shadow: 0 20px 20px rgba(0, 0, 0, 0.5), 0 0 50px rgba(0, 0, 0, 0.2);
}

.pc-info {
    text-align: center;
}



.pc-desc {
    font-size: 1.3rem;
    padding-top: 2rem 0;
    color: #ccb6b6;
    font-weight: lighter;
    transition: all 0.75s ease-out;
}

.pc-title {
    font-size: 2.5rem;
    transition: all 0.75s ease-out;
    margin: 10px 0;
    color: rgb(255, 255, 255);
    background: linear-gradient(
        to right,
        rgb(255, 0, 128),
        rgb(0, 132, 255),
        rgb(0, 225, 255),
        /* rgb(0, 255, 170), */
        rgb(0, 255, 128)
    );
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    -webkit-filter: drop-shadow(0px 5px 7px rgb(10, 10, 10));
            filter: drop-shadow(0px 5px 7px rgb(10, 10, 10));
    font-family: var(--dosis-font);
    text-transform: uppercase;
}




